type KoText = {
   hangul: string;
   rr: string;
 };
type ActNameAdjective = {
   en: string;
   es_m_s: string;
   es_f_s: string;
   es_m_p: string;
   es_f_p: string;
   ko: KoText;
 };
type ActNameNoun = {
   en: { s: string; p: string };
   es: { gender: "m" | "f"; s: string; p: string };
   ko: { s: KoText; p: KoText };
 };
